body {
  color: #fff;
  background-color: #0e2041;
}

.container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form {
  box-sizing: border-box;
  height: 500px;
  width: 320px;
  background-color: #15172b;
  border-radius: 20px;
  padding: 20px;
}

.title {
  color: #eee;
  margin-top: 10px;
  font-family: sans-serif;
  font-size: 16px;
  font-weight: 600;
}

.input-container {
  height: 50px;
  width: 100%;
  margin-top: 30px;
  position: relative;
}

.input {
  box-sizing: border-box;
  color: #eee;
  height: 100%;
  width: 100%;
  background-color: #303245;
  border: 0;
  border-radius: 12px;
  outline: 0;
  padding: 4px 20px 0;
  font-size: 18px;
}

.cut {
  height: 20px;
  width: 100px;
  background-color: #15172b;
  border-radius: 10px;
  transition: transform .2s;
  position: absolute;
  top: -20px;
  left: 20px;
  transform: translateY(0);
}

.cut-short {
  width: 60px;
}

.input:focus ~ .cut, .input:not(:placeholder-shown) ~ .cut {
  transform: translateY(8px);
}

.placeholder {
  color: #65657b;
  pointer-events: none;
  transform-origin: 0;
  font-family: sans-serif;
  line-height: 14px;
  transition: transform .2s, color .2s;
  position: absolute;
  top: 20px;
  left: 20px;
}

.input:focus ~ .placeholder, .input:not(:placeholder-shown) ~ .placeholder {
  transform: translateY(-30px)translateX(10px)scale(.75);
}

.input:not(:placeholder-shown) ~ .placeholder {
  color: #808097;
}

.input:focus ~ .placeholder {
  color: #dc2f55;
}

